.mob-menu {
  position: absolute;
  top: 48px;
  right: 0;

  background-color: #191C1D;

  &__wrapp {
    position: relative;
  }

  &__btn-subscribe {
    display: flex;
    padding: 10px 24px;
    justify-content: center;
    align-items: center;
    column-gap: 8px;

    border-radius: 16px;
    border: none;
    background-color: #E9C400;

    @media (min-width: 1200px) {
      display: none;
    }
  }

  @media (min-width: 790px) and (max-width: 1199px) {
    top: 55px;
    border-radius: 10px;
  }

  opacity: 1;

  transition: all 300ms linear;
}

.is-hidden-mob-menu {
  opacity: 0;
  transform: translateY(-110%);

  @media (min-width: 790px) and (max-width: 1199px) {
    opacity: 0;
    transform: translateY(-170%);
  }
}

.mob-menu__container {
  padding: 40px;

  @media (min-width: 790px) and (max-width: 1199px) {
    display: flex;
    column-gap: 24px;

    padding: 15px;
  }
}

.mob-menu__nav {
  display: flex;
  flex-direction: column;
  row-gap: 32px;

  @media (min-width: 790px) {
    display: none;
  }

  @media (min-width: 1200px) {
    display: flex;
    align-items: center;
  }
}

.mob-menu__link {
  display: flex;
  align-items: center;
  gap: 0 8px;

  &-text {
    font-size: 22px;
    font-style: normal;
    font-weight: 800;
    line-height: calc(28 / 22);
    color: #968E98;

    @media (min-width: 1200px) {
      display: none;
    }

    &_active {
      color: var(--accent-text-color);
    }
  }
}

.mob-menu__icon {
  width: 24px;
  height: 24px;
  fill: #968E98;

  &_active {
    fill: var(--accent-text-color);
  }

  // &-btn-close {
  //   width: 32px;
  //   height: 32px;
  // }
}

// ----mob-menu-socials-------------------

.socials-menu {
  margin: 40px 0;

  @media (min-width: 790px) and (max-width: 1199px) {
    margin: 0;
  }

  &__list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 16px 24px;

    @media (min-width: 790px) and (max-width: 1199px) {
      display: flex;
    }

    @media (min-width: 1200px) {
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 24px 0;
    }
  }

  @media (min-width: 1200px) {
    margin-bottom: 0;
  }
}

.socials-menu__link {
  background-color: #af3333;
  padding: 16px;
  display: flex;
  border-radius: 16px;

  &_patreon {
    background-color: #323536;
  }

  &_telegram {
    background-color: #004395;
  }

  &_TikTok {
    background-color: #544600;
  }

  &_Twitter {
    background-color: #005311;
  }
}

.socials-menu__icon {
  width: 24px;
  height: 24px;
}